.toggle-button{
    height: 24px;
    width: 36px;
    background: transparent;
    border: none;
    cursor: pointer;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}

.toggle-button:focus{
    outline: none;
}

.toggle-button__line{
    width: 30px;
    height: 2px;
    background: #111
}